.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1100px;
    grid-template-areas: "h h" "n c";

    grid-template-rows: 40px 1fr;
    grid-template-columns: 2fr 10fr;
     grid-gap: 10px;
}

.app-wrapper-content {
    grid-area: c;
    background-color: #edeef0;
    border-radius: 5px;
}










